
import { computed, defineComponent, reactive } from 'vue';

const propsData = {
  color: {
    type: String,
    require: true,
    default: 'primary',
  },
  type: {
    type: String,
    require: true,
    default: 'round',
  },
  size: {
    type: String,
    require: true,
    default: 'regular',
  },
  disabled: {
    type: Boolean,
    default: false,
  },
};

interface ButtonProps {
  color: string;
  type: string;
  size: string;
  disabled: boolean;
}

const MermaidUIButton = defineComponent({
  name: 'mi-button',
  emits: ['click'],
  props: propsData,
  setup(props: ButtonProps) {
    const getProps = reactive(props);
    const computedStyleClass = computed(() => {
      const { color, type } = getProps;
      return `mmui__button--${color} mmui__button--type-${type}-${color}`;
    });
    return { computedStyleClass, getProps };
  },
});

export default MermaidUIButton;
